<template><section><h1>message</h1>
<template>
    <coding :code="code" title="基本" content="只需要传入2个参数，一个是文本，一个是消失时间" lang="js">
        <y-button type="primary" @click.native="test">info</y-button>
        <y-button type="primary" @click.native="test1">success</y-button>
        <y-button type="primary" @click.native="test2">warning</y-button>
        <y-button type="primary" @click.native="test3">danger</y-button>
    </coding>
</template>

<h2>参数设置</h2>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>content</td>
<td>内容</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>time</td>
<td>消失时间</td>
<td><code>Number</code></td>
<td>1500</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data(){
        return {
            code:
`this.$message.info("info", 2000)
this.$message.success("success", 2000)
this.$message.warning("warning", 2000)
this.$message.danger("danger", 2000)
`
        }
    },
    methods:{
        test(){
            this.$message.info("info", 2000)
        },
        test1(){
            this.$message.success("success", 2000)
        },
        test2(){
            this.$message.warning("warning", 2000)
        },
        test3(){
            this.$message.danger("danger", 2000)
        }
    }
}
</script>


